import React, { Component } from "react";
import "./../App.css";
import Tabletop from "tabletop";
import ImageMapper from "react-image-mapper";
import Introduction from "./introduction";
import img_bg_slide0 from "./images/img_bg_slide0.jpg";

export default class Timeline extends Component {
  constructor() {
    super();
    this.state = {
      data: []
    };
  }

  componentDidMount() {
    Tabletop.init({
      key: "1kGawtJVtMNaAExb_nEqauE79cTAIzcWdJwUGY0JIYaI",
      callback: googleData => {
        console.log("google sheet data --->", googleData);
        this.setState({
          data: googleData
        });
      },
      simpleSheet: true
    });
  }

  render() {
    const { data } = this.state;
    return (
      <div>
        <section className="colorlib-experience" data-section="timeline">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div
                className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box"
                data-animate-effect="fadeInLeft"
              >
                <span className="heading-meta">ข้อมูลด้าน</span>
                <h2 className="colorlib-heading animate-box">
                  การปฏิบัติงานและกิจกรรมที่ได้เข้าร่วม
                </h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article
                    className="timeline-entry animate-box"
                    data-animate-effect="fadeInTop"
                  >
                    {data.map(obj => {
                      var a = obj.Picture1 + "?auto=compress&cs=tinysrgb&h=350";
                      var im1 = obj.Picture1.replace("open?", "uc?");
                      var im2 = obj.Picture2.replace("open?", "uc?");
                      var im3 = obj.Picture3.replace("open?", "uc?");
                      var v2 = "inline-block";
                      var v3 = "inline-block";
                      if (im2 == null || im2 == "") {
                        im2 = im1;
                        v2 = "none";
                      }
                      if (im3 == null || im3 == "") {
                        im3 = im1;
                        v3 = "none";
                      }

                      var head1 = obj.Topic1.localeCompare("");
                      var head2 = obj.Topic2.localeCompare("");
                      var head3 = obj.Topic3.localeCompare("");
                      if (head1 != 0) {
                        head1 = "#" + obj.Topic1;
                      } else {
                        head1 = obj.Topic1;
                      }
                      if (head2 != 0) {
                        head2 = "#" + obj.Topic2;
                      } else {
                        head2 = obj.Topic2;
                      }
                      if (head3 != 0) {
                        head3 = "#" + obj.Topic3;
                      } else {
                        head3 = obj.Topic3;
                      }
                      var dateD = obj.Date;
                      var lastFour = dateD.substring(dateD.length - 4, dateD.length);
                      var firstOne = dateD.substring(0, dateD.length -4);
                      return (
                        <div key={obj.Title}>
                          <div className="timeline-entry-inner">
                            <div className="timeline-icon color-5">
                              <i className="icon-pen2" />
                            </div>
                            <div className="timeline-label">
                              <h2
                               className="btn-titled"
                                style={{ fontSize: "16px" }}
                              >
                                {obj.Title} : วันที่ {firstOne}{parseInt(lastFour)+543}
                              </h2>
                              <br></br>
                              <div style={{marginTop: -20}}>
                                <h1>tag:</h1>
                                <a href="">
                                  <h1>{head1}</h1>
                                </a>
                                <a href="">
                                  <h1>{head2}</h1>
                                </a>
                                <a href="">
                                  <h1>{head3}</h1>
                                </a>
                              <br></br>
                              <img className="photo" alt="" src={im1} />
                              <img
                                className="photo"
                                alt=""
                                src={im2}
                                style={{ display: v2 }}
                              />
                              <img
                                className="photo"
                                alt=""
                                src={im3}
                                style={{ display: v3 }}
                              />

                              <p>{obj.Detail}</p></div>
                            </div>
                          </div>
                          <br></br>
                        </div>
                      );
                    })}
                  </article>
                  <article
                    className="timeline-entry animate-box"
                    data-animate-effect="fadeInLeft"
                  >
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>
                          เริ่มปฏิบัติราชการวันแรก <span>11 เมษายน 2018</span>
                        </h2>
                        <p>
                          รายงานตัวและเข้ารับราชการในตำแหน่ง ครูผู้ช่วย
                          เอกการสอนคอมพิมเตอร์ โรงเรียนภูเขียว อำเภอภูเขียว
                          จังหวัดชัยภูมิ ในวันที่ 11 เดือนเมษายน พ.ศ. 2561
                        </p>
                      </div>
                    </div>
                  </article>
                  <article
                    className="timeline-entry begin animate-box"
                    data-animate-effect="fadeInBottom"
                  >
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none"></div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}
